import React, { useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { Table, Button, Row, Col } from 'react-bootstrap'
//import useDispatch and useSelector so we can deal with our redux state
import { useDispatch, useSelector } from 'react-redux'
import Message from '../commponents/Message'
import Loader from '../commponents/Loader'
import Paginate from '../commponents/Paginate'
import { listProducts, deleteProduct } from '../actions/productActions'


const ProductListScreen = ({ history, match} ) => {
    const pageNumber = match.params.pageNumber || 1

    const dispatch = useDispatch();

    const productList = useSelector(state => state.productList)
    const { loading, error, products, page, pages } = productList


    const productDelete = useSelector(state => state.productDelete)
    const { loading: loadingDelete, error:errorDelete} = productDelete


    const productCreate = useSelector(state => state.productCreate)
    const { loading: loadingCreate, error:errorCreate, success: successCreate, product: createdProduct} = productCreate

    //because we want the user who does't admin block to see this page, if else down
    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin


    //as soon as useEffect runs, we want to dispatch the productCreateReset
    useEffect(() => {
        //dispatch(productCreateReset());

        if (!userInfo.isAdmin) {
            history.push('/login');
        }

        dispatch(listProducts('', pageNumber))

    }, [dispatch, history, userInfo, successCreate, createdProduct, pageNumber])

    const deleteHandler = (id) => {
        if (window.confirm('Are you sure?')){
            dispatch(deleteProduct(id))
        }
    }
    

    const createProductHandler = () => {
        //create product
        history.push('/admin/product/create');
    }

  return (
    <>
        <Row className='align-items-center'>
            <Col>
                <h1>Products</h1>
            </Col>
            <Col className='text-right'>
                <Button className='my-3' onClick={createProductHandler}>
                    <i className='fas fa-plus'></i>
                    Create Product
                </Button>
            </Col>
        </Row>
        {loadingDelete && <Loader />}
        {errorDelete && <Message variant='danger'>{errorDelete}</Message>}
        {loadingCreate && <Loader />}
        {errorCreate && <Message variant='danger'>{errorCreate}</Message>}
      {loading ? (
        <Loader />
        ) : error ? (
            <Message variant='danger'>{error}</Message>
        ) : (
            <>
            <Table striped hover responsive className='table-sm'>
                <thead>
                    <tr>
                        <th>ID</th>
                        <th>NAME</th>
                        <th>PRICE</th>
                        <th>CATEGORY</th>
                        <th>BRAND</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {products.map(product => (
                        <tr key={product._id}>
                            <td>{product._id}</td>
                            <td>{product.name}</td>
                            <td>{product.price}€</td>
                            <td>{product.category}</td>
                            <td>{product.brand}</td>
                            <td>
                                <LinkContainer to={`/admin/product/${product._id}/edit`}>
                                    <Button variant='light' className='btn-sm'>
                                        <i className='fas fa-edit'></i>
                                    </Button>
                                </LinkContainer>
                                <Button variant='danger' className='btn-sm' onClick={() => deleteHandler(product._id)}>
                                    <i className='fas fa-trash'></i>
                                </Button>
                            </td>
                        </tr>
                    ))}
                </tbody>
            </Table>
            <Paginate pages={pages} page={page} isAdmin={true}/>
            </>
        )}
    </>
  )
}

export default ProductListScreen;
