import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import { Row, Col, ListGroup, Image, FormControl, Button, Card} from 'react-bootstrap'
import Message from '../commponents/Message'
import { addToCart, removeFromCart } from '../actions/cartActions'


const CartScreen = ({ match, location, history}) => {

  const productId = match.params.id

  //sve sto se nalazi iza ? u linku (url) se nalazi na ovoj lokaciji primer ?qty=1
  //location.search.split, radi split pomocu = znaka gde je ?qty 0 indeks a 1 sto nama treba 1 zato stavljamo [1]
  // : 1 --- else qty ce biti 1
  //Number kakobi nam dao broj
  const qty = location.search ? Number(location.search.split('=')[1]) : 1

  const dispatch = useDispatch()

  const cart = useSelector(state => state.cart)
  const { cartItems } = cart;

  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId, qty)) // productId i qty dobija iz location.search odnosno iz URL
    }
  }, [dispatch, productId, qty])

  const removeFromCartHandler = (id) => {
    dispatch(removeFromCart(id))
  }

  const checkoutHandler = () => {
    history.push('/login?redirect=shipping')
  }

//item.product is ID
  return (
    <Row>
      <Col md = {8}>
        <h1>Shopping Cart</h1>
        {cartItems.length === 0 ? (
          <Message>
            Your cart is empty <Link to = '/'>Go Back</Link>
          </Message>
        ) : (
          <ListGroup variant='flush'>
            {cartItems.map(item => (
              <ListGroup.Item key={item.product}> 
                <Row>
                  <Col md = {2}>
                    <Image src={item.image} alt={item.name} fluid rounded />
                  </Col>
                  <Col md = {3}>
                    <Link to={`/product/${item.product}`}>{item.name}</Link>
                  </Col>
                  <Col md = {2}>
                  {item.price}€
                  </Col>
                  <Col md = {2}>
                    <FormControl 
                      as = 'select' 
                      value = {item.qty} 
                      onChange = {(e) => dispatch(addToCart(item.product, Number(e.target.value)))}
                    >
                       {[...Array(item.countInStock).keys()].map(x => (
                        <option key = {x + 1} value = {x + 1}>
                          {x + 1}
                        </option>
                       ))}
                    </FormControl>
                  </Col>
                  <Col md = {2}>
                    <Button type='button' variant='light' onClick={() =>
                    removeFromCartHandler(item.product)}>
                      <i className='fas fa-trash'></i>
                    </Button>
                  </Col>
                </Row>
              </ListGroup.Item>
            ))}
          </ListGroup>
        )}
      </Col>
      <Col md = {4}>
        <Card>
          <ListGroup variant='flush'>
            <ListGroup.Item>
              <h2>
                Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)}
                ) items
              </h2>
              {cartItems.reduce((acc, item) => acc + item.qty * item.price, 0).toFixed(2)}€
            </ListGroup.Item>
            <ListGroup.Item>
              <Button 
                type='button'
                className='btn-block' 
                disabled={cartItems.length === 0} 
                onClick={checkoutHandler}
              >
                Proceed To Checkout
              </Button>
            </ListGroup.Item>
          </ListGroup>
        </Card>

      </Col>
    </Row>
  )
}

export default CartScreen
